
.lesson-page{
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    min-height: 50%;
}

.lesson-list-container{
    flex: 1; 
    flex-direction: row;
}

.lesson-list {
    display: flex;
    list-style-type: none;
    text-align: center;
    overflow: scroll;
    flex: 1;
    flex-direction: row;
}


.lesson-list::-webkit-scrollbar {
    background-color: #33333300;
}

.lesson-list::-webkit-scrollbar-thumb{
    background-color: #73dcdc;
    border-radius: 2em;
}

.lesson-list::-webkit-scrollbar-corner { 
    background: rgba(0,0,0,0.0); 
}

.lesson-list-item {
    padding: 1em;
    margin-left: 2em;
}

.lesson-plan {
    flex: 2;
    display: flex;
    border-radius: 2rem;
    flex-direction: column;
    justify-content: space-evenly;
    min-height: 60vh;
    
}

.lesson-list-header{
    color: #426696;
    font-weight: 600;
    font-size: 2rem;
    opacity: 0.8;
    margin-left: 2em;
    margin-top: 1em;
}

.lesson-plan-header, .lesson-plan-body {
    color: #465974;
    font-weight: 500;
    opacity: 0.8;
    margin-bottom: 3rem;
    padding-left: 3rem;
}

.lesson-plan-body {
    padding-right: 2em;
    min-height: 50%;
}

.lesson-plan-header{
    padding-top: 2rem;
}

.lesson-list-container::-webkit-scrollbar {
    background-color: #33333300;
  }
  
.lesson-list::-webkit-scrollbar-thumb{
    background-color: #73dcdc;
    border-radius: 2em;
}

@media only screen and (min-width: 550px) {
    .lesson-page{
        flex-direction: row;
    }
    .lesson-plan-header{
        padding-top: 2rem;
    }

    .lesson-plan-header, .lesson-plan-body {
        color: #465974;
        font-weight: 500;
        opacity: 0.8;
        margin-bottom: 3rem;
        padding-left: 3rem;
    }

    .lesson-list-container{
        flex: 1; 
        flex-direction: column;
    }

    .lesson-list {
        flex: 1;
        max-height: 60vh;
        direction: rtl;
    }

    .lesson-list {
        display: block;
        list-style-type: none;
        text-align: center;
        overflow: scroll;
        flex: 1;
        max-height: 60vh;
        flex-direction: row;
    }
}